import { extend, override } from 'flarum/common/extend';
import app from 'flarum/app';
import HeaderSecondary from 'flarum/components/HeaderSecondary';
import SettingsPage from 'flarum/components/SettingsPage';
import LogInButton from 'flarum/forum/components/LogInButton';
import LogInModal from 'flarum/components/LogInModal';

app.initializers.add('tbbid-oauth-passport', () => {

    extend(HeaderSecondary.prototype, 'items', function(items) {
        items.remove('signUp');

        if(!items.has('logIn')) {
            return;
        }

        items.replace('logIn', 
            LogInButton.component(
                {
                    className: 'Button Button--link',
                    path: '/auth/passport'
                },
                app.forum.attribute('fof-passport.loginTitle')
            )
        );
    });

    extend(LogInModal.prototype, 'oninit', function(original, event) {
        // Hide modal login
        this.hide();

        const width = 580;
        const height = 400;
        const $window = $(window);

        window.open(
            app.forum.attribute('baseUrl') + '/auth/passport',
            'logInPopup',
            `width=${width},` +
            `height=${height},` +
            `top=${$window.height() / 2 - height / 2},` +
            `left=${$window.width() / 2 - width / 2},` +
            'status=no,scrollbars=yes,resizable=no'
        );
    });

    extend(SettingsPage.prototype, 'accountItems', function(items) {
        items.remove('changeEmail');
        items.remove('changePassword');

        if(items.has('account') &&
           items.get('account').props.children.length === 0)
        {
            items.remove('account');
        }
    });

});
